import React, { useState } from 'react'
import './navbar.css'
import { Link } from 'react-router-dom'
import {FaTimes,FaBars, FaHandHolding} from 'react-icons/fa'
export default function Navbar() {
    const [ burger,setBurger ] = useState(false)
    const [ contact,setContact] = useState(false)
    return (
        <div className="navbar">
            {/* <a className="nav-logo"><Link to="/">alqinsidev</Link></a> */}
            <div className="menu-icon" onClick={()=>setBurger(!burger)}>
                {
                    burger?<FaTimes/>:<FaBars/>
                }
            </div>
            <ul className={burger?"nav-menu active":"nav-menu"}>
                <Link className="nav-item" to="/">HOME</Link>
                <Link className="nav-item" to="/">ABOUT ME</Link>
                <Link className="nav-item" to="/">MY WORK</Link>
                <Link className="nav-item" to="/" onMouseEnter={()=>setContact(!contact)} onMouseLeave={()=>setContact(!contact)} onClick={()=>window.open('mailto:padlanalqinsi@live.com')}>CONTACT <span className={(!contact)?"nav-item-accent":null}>ME</span></Link>
            </ul>
        </div>
    )
}
