import React, {useState,useEffect} from 'react'
import './home.css'
import myPhoto from '../../assets/image/handsome-man.png'
export default function Home() {
    const [typing,setTyping] = useState('')
    const [x,setX] = useState(0);
    const word = "Welcome to my world !"
    const delay = ms => new Promise(resolve => setTimeout(resolve, ms))

    const ty = async ()=>{
        const wordArr = word.split(' ');
        let wait = 70
        if(x<word.length)
        {
            if(x == wordArr[0].length + 1) wait = 800;
            if(x > wordArr[0].length + 1) wait = 60
            await delay(wait);
            setTyping(typing + word[x])
            setX(x+1)
        }
    }
    useEffect(() => {
        ty()  
    },[x])
    return (
        <div>
            <div className="section home">
            <div className="row">
                        <div className="col">
                            <div className="jumbotron-image-container">
                                <img
                                    className="jumbotron-image"
                                    src={myPhoto}
                                />
                            </div>
                        </div>
                        <div className="col">
                            <div className="jumbotron-text-container">
                                <h3 className="jumbotron-text">{typing}</h3>
                            </div>
                        </div>
                    </div>
            </div>
            <div className="section">

            </div>
        </div>
    )
}
