import React from 'react'
import './footer.css'
import Logo from '../../logo.svg'
import {FaLinkedin,FaGithubSquare, FaInstagramSquare, FaTwitterSquare} from 'react-icons/fa'
export default function index() {
    const handleLink = link => window.open(link,"_blank")
    return (
        <div className="footer">
            <div className="footer-container">
                <h5>Visit me on</h5>
                <div className="row footer-row">
                <FaLinkedin className="footer-icon" onClick={()=>handleLink('https://www.linkedin.com/in/alqinsi/')}/>
                <FaGithubSquare className="footer-icon" onClick={()=>handleLink('https://github.com/alqinsidev')}/>
                <FaInstagramSquare className="footer-icon" onClick={()=>handleLink('https://www.instagram.com/alqinsi/')}/>
                <FaTwitterSquare className="footer-icon" onClick={()=>handleLink('https://twitter.com/padlanalqinsi')}/>
                </div>
                {/* <p>Padlan Alqinsi 2021</p> */}
                <div className="row footer-row">
                    <p>Website created with</p>
                    <img className="footer-logo" src={Logo} onClick={()=>handleLink('https://reactjs.org')}/>
                </div>
            </div>
        </div>
    )
}
